import React from "react";
import styled from "styled-components";

const ListStyled = styled.ul`
  display: inline-block;
  font-size: 18px;
  list-style-type: none;
  margin-top: 20px;
  margin: 0 auto;
  vertical-align: top;
  width: 50%;
`;

const SectionStyled = styled.section`
  margin: 50px auto 0;
  opacity: 0;
  transition: opacity .2s;
  width: 80%;

  &.visible {
    opacity: 1;
  }
`;

const AnswerArchive = ({ archive }) => (
  <SectionStyled className={archive && archive.length > 0 ? "visible" : ""}>
    <header>
      <h2>Archive</h2>
    </header>
    <hr style={{margin: "0 0 20px 0"}} />
    <div>
      <ListStyled>
        {archive.length > 0 &&
          archive.filter(({ wasCorrect }) => wasCorrect).map(({ item: { a: answer, q: question } }, idx, arr) => (
            <li key={answer+question} style={{ color: "green" }}>
              {arr.length - idx}. {answer} - {question}
            </li>
          ))}
      </ListStyled>
      <ListStyled>
        {archive.length > 0 &&
          archive.filter(({ wasCorrect }) => !wasCorrect).map(({ item: { a: answer, q: question }, value }, idx, arr) => (
            <li key={answer+question} style={{ color: "red" }}>
            {arr.length - idx}. {answer} - {question} - {value || "/"}
            </li>
          ))}
      </ListStyled>
      </div>
      <br clear="both" />
  </SectionStyled>
);
export default AnswerArchive;
